"use client";

import Loading from "@/ui/Loading";
import React, { useEffect, useState } from "react";

interface BridgeCardProps {
  structureNumber: string | null; // The selected structure number
}

const BridgeCard: React.FC<BridgeCardProps> = ({ structureNumber }) => {
  const [bridgeData, setBridgeData] = useState<any>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    if (!structureNumber) return;

    const fetchBridgeData = async () => {
      setLoading(true);
      setError(null);

      try {
        const res = await fetch(`/api/bridge-info/${structureNumber}`);
        if (!res.ok) {
          throw new Error("Failed to fetch bridge details");
        }

        const data = await res.json();
        setBridgeData(data);
      } catch (err: any) {
        setError(err.message || "An error occurred while fetching data.");
      } finally {
        setLoading(false);
      }
    };

    fetchBridgeData();
  }, [structureNumber]);

  if (!structureNumber) {
    return (
      <div className="p-6 bg-white shadow-md rounded-md">
        Select a bridge to view details.
      </div>
    );
  }

  if (loading) {
    return <Loading />;
  }

  if (error) {
    return (
      <div className="p-6 bg-white shadow-md rounded-md text-red-500">
        {error}
      </div>
    );
  }

  if (!bridgeData) {
    return (
      <div className="p-6 bg-white shadow-md rounded-md">
        No data available for this bridge.
      </div>
    );
  }

  const { navigation, condition, maintenance, geometry, Structure, ...bridge } = bridgeData;

  return (
    <div className="p-6 bg-white shadow-md rounded-md w-full text-black space-y-6">
      <h2 className="text-xl font-bold">Bridge Details</h2>
      
      <section>
        <h3 className="text-lg font-semibold">General Information</h3>
        <p><strong>Structure Number:</strong> {bridge.structureNumber}</p>
        <p><strong>Location:</strong> {bridge.location}</p>
        <p><strong>Year Built:</strong> {bridge.yearBuilt}</p>
        <p><strong>Year Reconstructed:</strong> {bridge.yearReconstructed || "N/A"}</p>
        <p><strong>Latitude:</strong> {bridge.lat}</p>
        <p><strong>Longitude:</strong> {bridge.long}</p>
      </section>

      {navigation && (
        <section>
          <h3 className="text-lg font-semibold">Navigation</h3>
          <p><strong>Direction:</strong> {navigation.direction}</p>
          <p><strong>Traffic Lanes On:</strong> {navigation.trafficLanesOn}</p>
          <p><strong>Detour Kilos:</strong> {navigation.detourKilos || "N/A"}</p>
        </section>
      )}

      {condition && (
        <section>
          <h3 className="text-lg font-semibold">Condition</h3>
          <p><strong>Bridge Condition:</strong> {condition.bridgeCondition}</p>
          <p><strong>Deck Condition:</strong> {condition.deckCond}</p>
          <p><strong>Superstructure Condition:</strong> {condition.superstructureCond}</p>
        </section>
      )}

      {maintenance && (
        <section>
          <h3 className="text-lg font-semibold">Maintenance</h3>
          <p><strong>Maintenance Type:</strong> {maintenance.maintenanceType}</p>
          <p><strong>Roadway Improvement:</strong> {maintenance.roadwayImprovement || "N/A"}</p>
        </section>
      )}

      {geometry && (
        <section>
          <h3 className="text-lg font-semibold">Geometry</h3>
          <p><strong>Structure Length:</strong> {geometry.structureLength}m</p>
          <p><strong>Deck Width:</strong> {geometry.deckWidth}m</p>
          <p><strong>Roadway Width:</strong> {geometry.roadwayWidth}m</p>
        </section>
      )}

      {Structure && (
        <section>
          <h3 className="text-lg font-semibold">Structure</h3>
          <p><strong>Structure Kind:</strong> {Structure.structureKind}</p>
          <p><strong>Main Unit Spans:</strong> {Structure.mainUnitSpans}</p>
          <p><strong>Median Code:</strong> {Structure.medianCode}</p>
        </section>
      )}
    </div>
  );
};

export default BridgeCard;
